import React, { useState } from "react";
import { uploadFileToIPFS, uploadMetaData } from "../pinata";
import { mintNFT, loginMetamask } from "../utils/web3MintNFT";
import { Tmetadata } from "../types/types";
import "../css/MintForm.css"

const MintForm = () => {
  const [file, setFile] = useState<File | null>(null);
  const [name, setName] = useState("");
  const [desc, setDesc] = useState("");
  const [minting, setMinting] = useState(false);

  const handleMint = async () => {
    if (!file || !name || !desc) return alert("  .");

    setMinting(true);
    try {
      const address = await loginMetamask();

      const formData = new FormData();
      formData.append("file", file);
      const imageUrl = await uploadFileToIPFS(formData);

      const metadata: Tmetadata = {
        name,
        discription: desc,
        image: imageUrl,
      };
      const metadataUrl = await uploadMetaData(metadata);
      if(!metadataUrl){throw new Error('metadata  ')}
      const txHash = await mintNFT(metadataUrl, address);

      alert(" ! TX: " + txHash);
    } catch (err) {
      alert(`: ${err}`);
      console.log(err);
    }
    setMinting(false);
  };

  return (
    <div className="mint-form">
      <h2>NFT </h2>
      <input type="file" onChange={(e) => setFile(e.target.files?.[0] ?? null)} />
      <input placeholder="NFT " value={name} onChange={(e) => setName(e.target.value)} />
      <textarea placeholder="" value={desc} onChange={(e) => setDesc(e.target.value)} />
      <button onClick={handleMint} disabled={minting}>
        {minting ? " ..." : ""}
      </button>
    </div>
  );
};

export default MintForm;
